<template>
  <div class="bg">
    <div class="login_container">
      <div class="login_box">
        <!--  -->
        <div class="avatar_box">
          <img src="../assets/user.jpg" alt="" />
        </div>
        <!--  -->

        <el-form
          ref="loginFormRef"
          :model="loginFrom"
          :rules="loginFromRules"
          label-width="0px"
          class="login_form"
        >
          <!--  -->
          <el-form-item prop="username">
            <el-input
              v-model="loginFrom.username"
              prefix-icon="iconfont icon-user"
            ></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item prop="password">
            <el-input
              v-model="loginFrom.password"
              prefix-icon="iconfont icon-suo"
              show-password
            ></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item class="btns">
            <el-button type="primary" @click="login"></el-button>
            <el-button type="info" @click="resetLoginForm"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <el-dialog
      title=""
      :visible.sync="registerVisible"
      width="500px"
      top="3%"
      @close="registerClosed"
    >
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        class="register_form"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="nickname">
          <el-input v-model="addForm.nickname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex">
          <el-input class="input-role" v-model="addForm.sex">
            <el-select
              v-model="addForm.sex"
              slot="prepend"
              placeholder=""
            >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="age">
          <el-input v-model="addForm.age"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tel">
          <el-input v-model="addForm.tel"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="addForm.address"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="registerVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    // 
    let checkEmail = (rule, value, cb) => {
      const regEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (regEmail.test(value)) {
        // 
        return cb();
      }
      cb(new Error(""));
    };
    // 
    let checkMobile = (rule, value, cb) => {
      const regMobile = /^1[3456789]\d{9}$/;
      if (regMobile.test(value)) {
        // 
        return cb();
      }
      cb(new Error(""));
    };
    return {
      // 
      addForm: {
        username: "",
        password: "",
        nickname: "",
        sex: "",
        age: "",
        tel: "",
        address: "",
        roleIds: "",
      },
      // 
      addFormRules: {
        username: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 0,
            max: 10,
            message: " 3  10 ",
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 6,
            max: 15,
            message: " 6  15 ",
            trigger: "blur",
          },
        ],
        email: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          { validator: checkEmail, trigger: "blur" },
        ],
        tel: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 7,
            max: 11,
            message: " 7  11 ",
            trigger: "blur",
          },
        ],
      },

      //
      loginFrom: {
        username: "",
        password: "",
      },
      //
      loginFromRules: {
        // 
        username: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 0,
            max: 25,
            message: " 0  25",
            trigger: "blur",
          },
        ],
        // 
        password: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 4,
            max: 25,
            message: " 4  25 ",
            trigger: "blur",
          },
        ],
      },

      registerVisible: false,
    };
  },
  methods: {
    resetLoginForm() {
      console.log(this.registerVisible);
      this.registerVisible = true;
    },

    login() {
      this.$refs.loginFormRef.validate(async (valid, object) => {
        if (!valid) return;
        // 
        const { data: data } = await this.$http.post(
          "login/auth",
          {
            username: this.loginFrom.username,
            password: this.loginFrom.password,
          },
          {
            header: {
              "content-type": "application/json",
            },
          }
        );
        if (data.code !== "200")
          return this.$message.error("");
        else {
          window.sessionStorage.setItem("token", data.info.token);
          window.sessionStorage.setItem("rolerank", data.info.roleIds[0]);
          this.$message.success("");
          this.$router.push("/feralcat");
        }
      });
    },
    // 
    registerClosed() {
      this.$refs.addFormRef.resetFields();
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        let roleID = [Number(this.addForm.roleIds)];
        delete this.addForm.roleIds;
        this.addForm.roleIds = roleID;
        console.log("addForm", this.addForm);
        // 
        const { data: data } = await this.$http.post(
          "user/register",
          this.addForm
        );
        console.log("addUser", data);
        if (data.code == "200") {
          this.$message.success("");
          this.registerVisible = false;
          delete this.addForm.roleIds;
          this.addForm.roleIds = "";
          return 1;
        }
        this.$message.error("");
      });
    },
  },
};
</script>
// scoped
