<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/welcome' }"
                ></el-breadcrumb-item
            >
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <!--  -->
        <el-card>
            <!-- 
            <el-alert
                title=""
                type="warning"
                show-icon
                :closable="false"
            >
            </el-alert> -->
            
            <el-card class="oneself-card">
                <div slot="header" class="clearfix">
                    <span></span>
                    <el-button style="float: right; padding: 3px 0" type="text"
                        ></el-button
                    >
                </div>
                <div
                    v-for="(value, keyname) of userInfo"
                    :key="value"
                    class="text item"
                >
                    {{ keyname + " : " + value }}
                </div>
            </el-card>
            <el-card class="oneself-card">
                <div slot="header" class="clearfix">
                    <span>:<b>{{"\n"+loginrole}}</b></span>
                    <!-- <el-button style="float: right; padding: 3px 0" type="text"
                        ></el-button
                    > -->
                </div>
                <img :src="src" width="250px"/>
            </el-card>
        </el-card>
    </div>
</template>

<script>
export default {
    name: "Oneself",
    data() {
        return {
            // 
            userInfo: {},
            // 
            userPrime: [],
            // 
            selectedCateKeys: [],
            loginrole:"",
            src:""
        };
    },
    created() {
        this.getUserInfo();
    },
    methods: {
        async getUserInfo() {
            const { data: data } = await this.$http.post("login/getInfo");
            if (data.code !== "200") {
                return this.$message.error("");
            }
            this.userInfo = {
                : data.info.username,
                : data.info.nickname,
            };
            console.log(data.info.roleIds[0]);
            let rolenum = data.info.roleIds[0];
            if(rolenum == 1){
                this.loginrole = ""
                this.src = require("../../assets/adm.png")
            }else{
                 this.loginrole = ""
                 this.src = require("../../assets/cat5.jpg")
            }
            this.userPrime = data.info.permissionList
        },
    },
};
</script>

