<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="7"
          ><el-input
            placeholder=""
            v-model="seekvalue"
            clearable
            @clear="closeUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="seekUserList"
            ></el-button> </el-input
        ></el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
        <el-col :span="4" v-if="rolerank">
          <el-button type="primary" @click="deleteDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <el-row :gutter="20" v-if="deleteDialogVisible" class="deletecla">
        <el-col :span="4">
          <el-button type="danger" @click="deleteUser"></el-button>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="redeleteUser"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table
        :data="lifelist"
        border
        stripe
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="index"> </el-table-column>
        <el-table-column
          label=""
          type="selection"
          v-if="deleteDialogVisible"
          :selectable="handleCheckedUser"
        >
        </el-table-column>
        <el-table-column label="" prop="lifeTitle"> </el-table-column>
        <el-table-column label="" prop="lifeContent"> </el-table-column>
        <el-table-column label="" prop="createTime"> </el-table-column>
        <el-table-column label="" prop="updateTime">
        </el-table-column>
        <el-table-column label="" width="180px">
          <template v-slot:default="slotProps">
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-user"
                size="mini"
                @click="showEditDialog(slotProps.row)"
              ></el-button>
            </el-tooltip>
            <!-- -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
              v-if="rolerank"
            >
              <el-button
                type="danger"
                icon="el-icon-edit"
                size="mini"
                @click="editEditDialog(slotProps.row)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
              v-if="rolerank"
            >
              <el-button
                type="waring"
                icon="el-icon-setting"
                size="mini"
                @click="setRole(slotProps.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="50%"
        @close="addDialogClosed"
      >
        <!--  -->
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          label-width="70px"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.lifeTitle"></el-input>
          </el-form-item>
          <el-form-item label="" prop="nickname">
            <el-input type="textarea" v-model="addForm.lifeContent"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="showDialogVisible"
        width="50%"
        @close="showDialogClosed"
      >
        <el-form
          :model="showForm"
          :rules="showFormRules"
          ref="editFormRef"
          label-width="70px"
        >
          <el-form-item label="">
            <el-input v-model="showForm.lifeTitle" disabled></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              type="textarea"
              v-model="showForm.lifeContent"
              disabled
            ></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="showDialogVisible = false"> </el-button>
          <el-button type="primary" @click="showDialogVisible = false"
            > </el-button
          >
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClosed"
      >
        <el-form
          :model="editForm"
          :rules="editFormRules"
          ref="editFormRef"
          label-width="70px"
        >
          <el-form-item label="">
            <el-input v-model="editForm.lifeTitle"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="editForm.lifeContent"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    // 
    let checkEmail = (rule, value, cb) => {
      const regEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (regEmail.test(value)) {
        // 
        return cb();
      }
      cb(new Error(""));
    };
    // 
    let checkMobile = (rule, value, cb) => {
      const regMobile = /^1[3456789]\d{9}$/;
      if (regMobile.test(value)) {
        // 
        return cb();
      }
      cb(new Error(""));
    };
    return {
      // 
      rolerank: true,
      // 
      queryInfo: {
        pagenum: 1,
        //
        pagesize: 5,
      },
      seekvalue: "",
      lifelist: [],
      total: 0,
      // 
      addDialogVisible: false,
      // 
      addForm: {
        lifeTitle: "",
        lifeContent: "",
      },
      // 
      addFormRules: {
        lifeTitle: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        lifeContent: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
      // 
      deleteDialogVisible: false,
      deletecheckedUsers: [],
      // 
      showDialogVisible: false,
      // 
      showForm: {},
      // 
      showFormRules: {
        lifeTitle: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        lifeContent: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        lifeTitle: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        lifeContent: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
      // 
      userinfo: {},
      // 
      rolesList: [],
      // id
      selectedRoleId: "",
      // ID
      deldatas: [],
    };
  },
  created() {
    this.getLifeList();
  },
  methods: {
    async getLifeList() {
      let rank = window.sessionStorage.getItem("rolerank");
      if (rank == 1) {
        this.rolerank = true;
      }
      const { data: data } = await this.$http.get("life/list", {
        params: {
          pageNum: this.queryInfo.pagenum,
          pageRow: this.queryInfo.pagesize,
        },
      });
      console.log(data);
      if (data.code == "200") {
        this.lifelist = data.info.list;
        this.total = data.info.totalCount;
        return 1;
      } else {
        return this.$message.error("");
      }
    },
    async seekUserList() {
      console.log(this.seekvalue);
      const { data: data } = await this.$http.get("life/listByLifeName", {
        params: {
          lifeTitle: this.seekvalue,
          pageNum: "1",
          pageRow: "10",
        },
      });
      if (data.code == "200") {
        this.lifelist = data.info.list;
        this.total = data.info.totalCount;
        return 1;
      } else {
        return this.$message.error("");
      }
    },
    closeUserList() {
      this.getLifeList();
    },
    //pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getLifeList();
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getLifeList();
    },
    //switch
    async userStateChanged(userinfo) {
      console.log(userinfo);
      // const { data: res } = await this.$http.put(
      //     `users/${userinfo.id}/state/${userinfo.mg_state}`
      // );
      // if (res.meta.status !== 200) {
      //     userinfo.mg_state = !userinfo.mg_state;
      //     return this.$message.error("");
      // }
      // this.$message.success("");
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(valid);
        // console.log(this.$refs.addFormRef.model);

        if (!valid) return this.$message.error("!");
        let roleID = [Number(this.addForm.roleIds)];
        delete this.addForm.roleIds;
        this.addForm.roleIds = roleID;
        console.log(this.addForm);
        // 
        const { data: data } = await this.$http.post(
          "life/addLife",
          this.addForm
        );
        console.log(data);
        if (data.code == "200") {
          this.$message.success("");
          this.addDialogVisible = false;
          this.getLifeList();
          delete this.addForm.roleIds;
          this.addForm.roleIds = "";
          return 1;
        }
        this.$message.error("");
      });
    },
    //
    showEditDialog(data) {
      console.log(data);
      this.showForm = data;
      this.showDialogVisible = true;
    },
    // 
    showDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    //
    editEditDialog(data) {
      console.log(data);
      this.editForm = data;
      this.editDialogVisible = true;
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    // 
    editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return this.$message.error("!");
        // 
        let req = {
          lifeTitle: this.editForm.lifeTitle,
          lifeContent: this.editForm.lifeContent,
          lifeId: this.editForm.lifeId,
          deleteStatus: "1",
        };
        const { data: data } = await this.$http.post("life/updateLife", req);
        // console.log(data);
        this.editDialogVisible = false;
        this.getLifeList();
        this.$message.success("");
        // console.log(res);
      });
    },
    // 
    handleCheckedUser(row, index) {
      return true;
    },
    handleSelectionChange(selection) {
      let dataarr = [];
      selection.filter((item) => {
        dataarr.push(item.lifeId);
        return item.lifeId;
      });
      this.deletecheckedUsers = dataarr;
    },
    // 
    async deleteUser() {
      console.log(this.deletecheckedUsers);
      const { data: data } = await this.$http.post("life/batchDeleteLife", {
        lifeId: this.deletecheckedUsers,
        deleteStatus: "2",
      });
      console.log(data);
      this.getLifeList();
      this.deleteDialogVisible = false;
    },
    // 
    redeleteUser() {
      this.deletecheckedUsers = [];
      this.deleteDialogVisible = false;
    },
    //id
    async removeUserById(id) {
      const confirmResult = await this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).catch((err) => err);
      // confirm cancel
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.delete(`life/${id}`);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getLifeList();
    },
    // 
    async setRole(item) {
      console.log(item);
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          const { data: data } = await this.$http.post("life/updateLife", {
            lifeTitle: item.lifeTitle,
            lifeContent: item.lifeContent,
            lifeId: item.lifeId,
            deleteStatus: "2",
          });
          console.log(data);
          if (data.code == "200") {
            this.getLifeList();
            return 1;
          }
          this.$message.error("");
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
};
</script>

