                <!--  -->
                <el-form
                    :model="addForm"
                    :rules="addFormRules"
                    ref="addFormRef"
                    label-width="70px"
                >
                    <el-form-item label="" prop="username">
                        <el-input v-model="addForm.catName"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="sex">
                        <el-input class="input-role" v-model="addForm.catSex">
                            <el-select
                                v-model="addForm.catSex"
                                slot="prepend"
                                placeholder=""
                            >
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-input>
                    </el-form-item>

                    <el-form-item label="" prop="nickname">
                        <el-input type="textarea" v-model="addForm.catDescription"></el-input>
                    </el-form-item>
                </el-form>
                <!--  -->
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addDialogVisible = false"
                        > </el-button
