                    :model="showForm"
                    :rules="showFormRules"
                    ref="editFormRef"
                    label-width="70px"
                >
                    <el-form-item label="">
                        <el-input
                            v-model="showForm.catName"
                            disabled
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="showForm.catSex" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input type="textarea" v-model="showForm.catDescription" disabled></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="showDialogVisible = false"
                        > </el-button
                    >
                    <el-button type="primary" @click="showDialogVisible = false"
                        > </el-button
                    >
                </span>
            </el-dialog>

            <!--  -->
            <el-dialog
                title=""
                :visible.sync="editDialogVisible"
                width="50%"
                @close="editDialogClosed"
            >
                <el-form
                    :model="editForm"
                    :rules="editFormRules"
                    ref="editFormRef"
                    label-width="70px"
                >
                    <el-form-item label="">
                        <el-input v-model="editForm.catName"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="catSex">
                        <el-input class="input-role" v-model="editForm.catSex">
                            <el-select
                                v-model="editForm.catSex"
                                slot="prepend"
                                placeholder=""
                            >
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-input>
                    </el-form-item>

                    <el-form-item label="">
                        <el-input type="textarea" v-model="editForm.catDescription"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="editDialogVisible = false"
                        > </el-button
                    >
                    <el-button type="primary" @click="editUserInfo"
                        > </el-button
                    >
                </span>
            </el-dialog>
        </el-card>
    </div>
</template>

<script>
export default {
    data() {
        // 
        let checkEmail = (rule, value, cb) => {
            const regEmail =
                /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
            if (regEmail.test(value)) {
                // 
                return cb();
            }
            cb(new Error(""));
        };
        // 
        let checkMobile = (rule, value, cb) => {
            const regMobile = /^1[3456789]\d{9}$/;
            if (regMobile.test(value)) {
                // 
                return cb();
            }
            cb(new Error(""));
        };
        return {
            // 
            rolerank: false,
            // 
            queryInfo: {
                pagenum: 1,
                //
                pagesize: 5,
            },
            seekvalue: "",
            teachlist: [],
            total: 0,
            // 
            addDialogVisible: false,
            // 
            addForm: {
                teachTitle: '',
                teachContent: ''
            },
            // 
            addFormRules: {
                teachTitle: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    }
                ],
                teachContent: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
            },
            // 
            deleteDialogVisible: false,
            deletecheckedUsers: [],
            // 
            showDialogVisible: false,
            // 
            showForm: {},
            // 
            showFormRules: {
                teachTitle: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    }
                ],
                teachContent: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
            },
            // 
            editDialogVisible: false,
            // 
            editForm: {},
            // 
            editFormRules: {
                teachTitle: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    }
                ],
                teachContent: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
            },
            // 
            userinfo: {},
            // 
            rolesList: [],
            // id
            selectedRoleId: "",
            // ID
            deldatas: [],
        };
    },
    created() {
        this.getTeachList();
    },
    methods: {
        async getTeachList() {
            let rank = window.sessionStorage.getItem("rolerank");
            if (rank == 1) {
                this.rolerank = true;
            }
            const { data: data } = await this.$http.get("cat/list", {
                params: {
                    pageNum: this.queryInfo.pagenum,
                    pageRow: this.queryInfo.pagesize,
                },
            });
            console.log(data);
            if (data.code == "200") {
                this.teachlist = data.info.list;
                this.total = data.info.totalCount;
                return 1;
            } else {
                return this.$message.error("");
            }
        },
        async seekUserList() {
            console.log(this.seekvalue);
            const { data: data } = await this.$http.get("cat/listByCatName", {
                params: {
                    catName: this.seekvalue,
                    pageNum: "1",
                    pageRow:"10",
                },
            });
            if (data.code == "200") {
                this.teachlist = data.info.list;
                this.total = data.info.totalCount;
                return 1;
            } else {
                return this.$message.error("");
            }
        },
        closeUserList() {
             this.getTeachList()
        },
        //pagesize
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize;
            this.getTeachList();
        },
        //
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage;
            this.getTeachList();
        },
        //switch
        async userStateChanged(userinfo) {
            console.log(userinfo);
            // const { data: res } = await this.$http.put(
            //     `users/${userinfo.id}/state/${userinfo.mg_state}`
            // );
            // if (res.meta.status !== 200) {
            //     userinfo.mg_state = !userinfo.mg_state;
            //     return this.$message.error("");
            // }
            // this.$message.success("");
        },
        // 
        addDialogClosed() {
            this.$refs.addFormRef.resetFields();
        },
        // 
        addUser() {
            this.$refs.addFormRef.validate(async (valid) => {
                // console.log(valid);
                // console.log(this.$refs.addFormRef.model);

                if (!valid)
                    return this.$message.error("!");
                let roleID = [Number(this.addForm.roleIds)];
                delete this.addForm.roleIds;
                this.addForm.roleIds = roleID;
                console.log(this.addForm);
                // 
                const { data: data } = await this.$http.post(
                    "cat/addCat",
                    this.addForm
                );
                console.log(data);
                if (data.code == "200") {
                    this.$message.success("");
                    this.addDialogVisible = false;
                    this.getTeachList();
                    delete this.addForm.roleIds;
                    this.addForm.roleIds = "";
                    return 1;
                }
                this.$message.error("");
            });
        },
        //
        showEditDialog(data) {
            console.log(data);
            this.showForm = data;
            this.showDialogVisible = true;
        },
        // 
        showDialogClosed() {
            this.$refs.editFormRef.resetFields();
        },
        //
        editEditDialog(data) {
            console.log(data);
            this.editForm = data;
            this.editDialogVisible = true;
        },
        // 
        editDialogClosed() {
            this.$refs.editFormRef.resetFields();
        },
        // 
        editUserInfo() {
            this.$refs.editFormRef.validate(async (valid) => {
                if (!valid)
                    return this.$message.error("!");
                // 
                let req = {
                    catName: this.editForm.catName,
                    catSex: this.editForm.catSex,
                    catDescription: this.editForm.catDescription,
                    catId: this.editForm.catId,
                    deleteStatus: "1",
                };
                const { data: data } = await this.$http.post(
                    "cat/updateCat",
                    req
                );
                // console.log(data);
                this.editDialogVisible = false;
                this.getTeachList();
                this.$message.success("");
                // console.log(res);
            });
        },
        // 
        handleCheckedUser(row, index) {
            return true;
        },
        handleSelectionChange(selection) {
            let dataarr = [];
            selection.filter((item) => {
                dataarr.push(item. catId);
                return item.teachId;
            });
            this.deletecheckedUsers = dataarr;
        },
        // 
        async deleteUser() {
            console.log(this.deletecheckedUsers);
            const { data: data } = await this.$http.post(
                "cat/batchDeleteCat",
                {
                     catId: this.deletecheckedUsers,
                    deleteStatus: "2",
                }
            );
            console.log(data);
            this.getTeachList();
            this.deleteDialogVisible = false;
        },
        // 
        redeleteUser() {
            this.deletecheckedUsers = [];
            this.deleteDialogVisible = false;
        },
        //id
        async removeUserById(id) {
            const confirmResult = await this.$confirm(
                "?",
                "",
                {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning",
                }
            ).catch((err) => err);
            // confirm cancel
            if (confirmResult !== "confirm") {
                return this.$message.info("");
            }
            const { data: res } = await this.$http.delete(`cat/${id}`);
            if (res.meta.status !== 200) {
                return this.$message.error("");
            }
            this.$message.success("");
            this.getTeachList();
        },
        // 
        async setRole(item) {
            console.log(item);
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            })
                .then(async () => {
                    const { data: data } = await this.$http.post(
                        "cat/updateCat",
                        {
                            catName: item.catName,
                            catSex: item.catSex,
                            catDescription: item.catDescription,
                            catId: item.catId,
                            deleteStatus: "2",
                        }
                    );
                    console.log(data);
                    if (data.code == "200") {
                        this.getTeachList();
                        return 1;
                    }
                    this.$message.error("");
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "",
                    });
                });
        },
    },
};
</script>

