        :page-sizes="[2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addDialogVisible"
        width="50%"
        @close="addDialogClosed"
      >
        <!--  -->
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          label-width="70px"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="addForm.password" show-password></el-input>
          </el-form-item>
          <el-form-item label="" prop="nickname">
            <el-input v-model="addForm.nickname"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sex">
            <el-input class="input-role" v-model="addForm.sex">
              <el-select
                v-model="addForm.sex"
                slot="prepend"
                placeholder=""
              >
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="age">
            <el-input v-model="addForm.age"></el-input>
          </el-form-item>
          <el-form-item label="" prop="tel">
            <el-input v-model="addForm.tel"></el-input>
          </el-form-item>
          <el-form-item label="" prop="address">
            <el-input v-model="addForm.address"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleIds">
            <el-input class="input-role" v-model="addForm.roleIds">
              <el-select
                v-model="addForm.roleIds"
                slot="prepend"
                placeholder=""
              >
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
              </el-select>
            </el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="showDialogVisible"
        width="50%"
        @close="showDialogClosed"
      >
        <el-form
          :model="showForm"
          :rules="showFormRules"
          ref="editFormRef"
          label-width="70px"
        >
          <el-form-item label="">
            <el-input v-model="showForm.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="nickname">
            <el-input v-model="showForm.nickname" disabled></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="showForm.tel" disabled></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="showForm.sex" disabled></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="showForm.age" disabled></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="showForm.address" disabled></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="showDialogVisible = false"> </el-button>
          <el-button type="primary" @click="showDialogVisible = false"
            > </el-button
          >
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClosed"
      >
        <el-form
          :model="editForm"
          :rules="editFormRules"
          ref="editFormRef"
          label-width="70px"
        >
          <el-form-item label="id">
            <el-input v-model="editForm.userId" disabled></el-input>
          </el-form-item>

          <el-form-item label="" prop="password">
            <el-input v-model="editForm.password" show-password></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="editForm.nickname"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleIds">
            <el-input class="input-role" v-model="editForm.roleIds">
              <el-select
                v-model="editForm.roleIds"
                slot="prepend"
                placeholder=""
              >
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
              </el-select>
            </el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    // 
    let checkEmail = (rule, value, cb) => {
      const regEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (regEmail.test(value)) {
        // 
        return cb();
      }
      cb(new Error(""));
    };
    // 
    let checkMobile = (rule, value, cb) => {
      const regMobile = /^1[3456789]\d{9}$/;
      if (regMobile.test(value)) {
        // 
        return cb();
      }
      cb(new Error(""));
    };
    return {
      // 
      rolerank: true,
      // 
      queryInfo: {
        pagenum: 1,
        //
        pagesize: 5,
      },
      seekvalue: "",
      userlist: [],
      total: 0,
      // 
      addDialogVisible: false,
      // 
      addForm: {
        username: "",
        password: "",
        nickname: "",
        sex: "",
        age: "",
        tel: "",
        address: "",
        roleIds: "",
      },
      // 
      addFormRules: {
        username: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 0,
            max: 10,
            message: " 3  10 ",
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 6,
            max: 15,
            message: " 6  15 ",
            trigger: "blur",
          },
        ],
        email: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          { validator: checkEmail, trigger: "blur" },
        ],
        tel: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 7,
            max: 11,
            message: " 7  11 ",
            trigger: "blur",
          },
        ],
      },
      // 
      deleteDialogVisible: false,
      deletecheckedUsers: [],
      // 
      showDialogVisible: false,
      // 
      showForm: {},
      // 
      showFormRules: {
        email: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          { validator: checkEmail, trigger: "blur" },
        ],
        mobile: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          { validator: checkMobile, trigger: "blur" },
        ],
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        email: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          { validator: checkEmail, trigger: "blur" },
        ],
        mobile: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          { validator: checkMobile, trigger: "blur" },
        ],
      },
      // 
      userinfo: {},
      // 
      rolesList: [],
      // id
      selectedRoleId: "",
      // ID
      deldatas: [],
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    async getUserList() {
      let rank = window.sessionStorage.getItem("rolerank");
      if (rank == 1) {
        this.rolerank = true;
      }
      const { data: data } = await this.$http.get("user/list", {
        params: {
          pageNum: this.queryInfo.pagenum,
          pageRow: this.queryInfo.pagesize,
        },
      });
      console.log(data);
      if (data.code == "200") {
        this.userlist = data.info.list;
        this.total = data.info.totalCount;
        return 1;
      } else {
        return this.$message.error("");
      }
    },
    async seekUserList() {
      console.log(this.seekvalue);
      const { data: data } = await this.$http.get("user/listByName", {
        params: {
          nickname: this.seekvalue,
          pageNum: "1",
          pageRow: "10",
        },
      });
      if (data.code == "200") {
        this.userlist = data.info.list;
        this.total = data.info.totalCount;
        return 1;
      } else {
        return this.$message.error("");
      }
    },
    closeUserList() {
      this.getUserList();
    },
    //pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getUserList();
    },
    //
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getUserList();
    },
    //switch
    async userStateChanged(userinfo) {
      console.log(userinfo);
      // const { data: res } = await this.$http.put(
      //     `users/${userinfo.id}/state/${userinfo.mg_state}`
      // );
      // if (res.meta.status !== 200) {
      //     userinfo.mg_state = !userinfo.mg_state;
      //     return this.$message.error("");
      // }
      // this.$message.success("");
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(valid);
        // console.log(this.$refs.addFormRef.model);

        if (!valid) return this.$message.error("");
        let roleID = [Number(this.addForm.roleIds)];
        delete this.addForm.roleIds;
        this.addForm.roleIds = roleID;
        console.log(this.addForm);
        // 
        const { data: data } = await this.$http.post(
          "user/addUser",
          this.addForm
        );
        console.log(data);
        if (data.code == "200") {
          this.$message.success("");
          this.addDialogVisible = false;
          this.getUserList();
          delete this.addForm.roleIds;
          this.addForm.roleIds = "";
          return 1;
        }
        this.$message.error("");
      });
    },
    //
    showEditDialog(data) {
      console.log(data);
      this.showForm = data;
      this.showDialogVisible = true;
    },
    // 
    showDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    //
    editEditDialog(data) {
      console.log(data);
      this.editForm = data;
      this.editDialogVisible = true;
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    // 
    editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return this.$message.error("");
        // 
        let req = {
          userId: this.editForm.userId,
          nickname: this.editForm.nickname,
          password: this.editForm.password,
          roleIds: [
            Number(this.editForm.roleIds)
              ? Number(this.editForm.roleIds)
              : Number("2"),
          ],
          deleteStatus: "1",
        };
        const { data: data } = await this.$http.post("user/updateUser", req);
        console.log(data);
        this.editDialogVisible = false;
        this.getUserList();
        this.$message.success("");
        // console.log(res);
      });
    },
    // 
    handleCheckedUser(row, index) {
      return true;
    },
    handleSelectionChange(selection) {
      let dataarr = [];
      selection.filter((item) => {
        dataarr.push(item.userId);
        return item.userId;
      });
      this.deletecheckedUsers = dataarr;
    },
    // 
    async deleteUser() {
      console.log(this.deletecheckedUsers);
      const { data: data } = await this.$http.post("user/batchDeleteUser", {
        userId: this.deletecheckedUsers,
        deleteStatus: "2",
      });
      console.log("", data);
      this.getUserList();
      this.deleteDialogVisible = false;
    },
    // 
    redeleteUser() {
      this.deletecheckedUsers = [];
      this.deleteDialogVisible = false;
    },
    //id
    async removeUserById(id) {
      const confirmResult = await this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).catch((err) => err);
      // confirm cancel
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.delete(`users/${id}`);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getUserList();
    },
    // 
    async setRole(item) {
      console.log(item);
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          const { data: data } = await this.$http.post("user/updateUser", {
            userId: item.userId,
            nickname: item.nickname,
            roleIds: [item.roles.roleId],
            deleteStatus: "2",
          });
          console.log(data);
          if (data.code == "200") {
            this.getUserList();
            return 1;
          }
          this.$message.error("");
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
};
</script>

<style lang="less" scoped>
.el-card {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15) !important;
}
